import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import PropTypes from 'prop-types';

import "./Alert.css";
import videochat from "./../../assets/svgs/video-chat.svg";
import observerchat from "./../../assets/svgs/Stop-Video.svg";


class AlertAskVideoObserver extends Component {

    render() {
        return (
            <div className="ask-alert">
                <div className="ask-alert-backdrop"></div>
                
                <div className="ask-alert-container">
                    <div className="ask-alert-header">Do you want to video chat or <br/> do you want to be an observer?</div>

                    <div className="ask-alert-content">
                        <div className="ask-alert-item" onClick={this.props.onVideoChat}>
                            <img src={videochat} alt="video chat"/>
                            <span className="ask-alert-item-text">Yes, I want to video chat</span>
                        </div>

                        <div className="ask-alert-item" onClick={this.props.onObserverChat}>
                            <img src={observerchat} alt="observer chat"/>
                            <span className="ask-alert-item-text">I'd like to watch please!</span>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

AlertAskVideoObserver.propTypes = {
    onVideoChat: PropTypes.func,
    onObserverChat: PropTypes.func,
};

export default withRouter(AlertAskVideoObserver);