import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import PropTypes from "prop-types";
import * as config from "../../config";
import {
    DefaultPromisedWebSocketFactory,
    DefaultDOMWebSocketFactory,
    FullJitterBackoff,
    ReconnectingPromisedWebSocket,
    MeetingSessionStatusCode
  } from 'amazon-chime-sdk-js';
import 'react-confirm-alert/src/react-confirm-alert.css';

// Components
// import VideoPlayer from "../videoPlayer/VideoPlayer";
import Chat from "../chat/Chat";
import Controls from "./Controls";
import Settings from "./Settings";
import Error from "./Error";
import LocalVideo from './LocalVideo';
import RemoteVideoGroup from './RemoteVideoGroup';
import Alert from './Alert';
import AlertWaiting from './AlertWaiting';
import AlertAskVideoObserver from './AlertAskVideoObserver';
import AlertHolding from './AlertHolding';
import AlertAskLeaveRoom from './AlertAskLeaveRoom';

// Styles
import "./ChimeWeb.css";
import "./Meeting.css";

import logo from "./../../assets/Logo.svg";
import bodylogo from "../../assets/UoL.svg";
import vicon from "../../assets/svgs/v-icon.svg";

const MAX_REMOTE_VIDEOS = config.CHIME_ROOM_MAX_ATTENDEE;

class Meeting extends Component {
    constructor() {
      super();

      this.baseHref = config.BASE_HREF;
      this.ssName = "";

      this.audioElementRef = React.createRef();
      this.myVideoElement = React.createRef();
      this.WEB_SOCKET_TIMEOUT_MS = 130000;
    }

    state = {
      meetingStatus: null, // Loading, Success or Failed
      showSettings: false,
      showError: false,
      errorMsg: "",
      showPopupMessage: false,
      isChat: true,
      isAvailable: false,

      messages: [],
      connection: null,
      participants: [],
      handRaiseStatus: true,
      muted: false,

      isLive: false,
      getStatus: 'waiting',
      isLocalCamera: true,
      isVideoChat: true,
      showAbleParts: [],
      isAsking: true,
      isAskForLeave: false,
      isOverFlow: false,
      getOverFlowStatus: 'waiting',
      isClosable: true,
    };

    showModal = () => {
      this.setState({ showPopupMessage: true });
    };

    openChat = () => {
        this.setState({ showPopupMessage: true, isChat: true });
    }

    openPT = () => {
        this.setState({ showPopupMessage: true, isChat: false });
    }

    changeChat = (index) => {
      if(index === 0) this.setState({ isChat: true });
      if(index === 1) this.setState({ isChat: false });
    }

    hideModal = () => {
        this.setState({ showPopupMessage: false });
    };

    componentDidMount() {
      
      const start = async () => {
        try {
          const qs = new URLSearchParams(this.props.location.search);
          const room = qs.get("room");
          this.ssName = `chime[${room}]`;
          if (!room || !sessionStorage.getItem(this.ssName)) {
              this.props.history.push(`${this.baseHref}/`);
          }

          const ssData = JSON.parse(sessionStorage.getItem(this.ssName));
          if (config.DEBUG) console.log(ssData);

          this.username = ssData.username;
          this.title = ssData.title;
          this.role = ssData.role;

          if (!ssData.joinInfo) {
              this.joinInfo = await this.props.chime.createRoom(
                  this.role,
                  this.username,
                  this.title,
                  ssData.playbackURL
              );
              const data = {
                  ...ssData,
                  joinInfo: this.joinInfo,
              };
              sessionStorage.setItem(this.ssName, JSON.stringify(data));
              this.playbackURL = this.joinInfo.PlaybackURL;
          } else {
              // Browser refresh
              this.joinInfo = ssData.joinInfo;
              this.playbackURL = ssData.joinInfo.PlaybackURL;
              await this.props.chime.reInitializeMeetingSession(
                  this.joinInfo,
                  this.username
              );
          }

          let blocks = JSON.parse(localStorage.getItem('blockedMeetings'));
          if(blocks) {
              blocks.forEach(block => {
                  if(block === this.joinInfo.Meeting.MeetingId) {
                      this.props.history.push(`${this.baseHref}/removed`);
                  }
              })
          }

          this.setState({ 
            meetingStatus: "Success",
            muted: this.role === 'host' ? false : true
          });

          if (this.role !== 'host') {
            this.props.chime.audioVideo.realtimeMuteLocalAudio();
          }

          this.props.chime.audioVideo.addObserver({
              audioVideoDidStop: async (sessionStatus) => {
                  if (
                      sessionStatus.statusCode() ===
                      MeetingSessionStatusCode.AudioCallEnded
                  ) {
                      const whereTo = `${this.baseHref}/${
                          this.role === "host" ? "" : "end"
                      }`;
                      this.props.chime.leaveRoom(this.role === "host");
                      this.props.history.push(whereTo);
                  }
              },
          });

          // this.props.chime.joinRoom(this.audioElementRef.current);

          // await this.props.chime.joinRoom(this.audioElementRef.current);

          // inverk init websocket connection
          // this.initChatConnection();
          this.askVideoOrObserver();
        } catch (error) {
          // eslint-disable-next-line
          console.error(error);
          this.setState({ meetingStatus: "Failed" });
        }
      };
      start();
      

      try{
        if(!navigator.mediaDevices.enumerateDevices) {
          this.setState({isAvailable: true});
        }
      } catch(e) {
        console.log(e)
        this.setState({isAvailable: true});
      }

      //   const isChrome = navigator.userAgent.indexOf("Chrome") !== -1;
      //   const isChrome = /Chrome/.test(navigator.userAgent) && /Google Inc/.test(navigator.vendor);
      const isIOS = /iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream;
        
      if(isIOS) {
        const isChrome = navigator.userAgent.indexOf("CriOS") !== -1;
        if(isChrome) {
          this.setState({isAvailable: true});
        }
      }
    }

    // init connection
    
  async initChatConnection() {
    const { Meeting, Attendee } = this.joinInfo;
    const messagingUrl = `${config.CHAT_WEBSOCKET}?MeetingId=${Meeting.MeetingId}&AttendeeId=${Attendee.AttendeeId}&JoinToken=${Attendee.JoinToken}`
    const connection = new ReconnectingPromisedWebSocket(
      messagingUrl,
      [],
      'arraybuffer',
      new DefaultPromisedWebSocketFactory(new DefaultDOMWebSocketFactory()),
      new FullJitterBackoff(1000, 0, 10000)
    );

    if (config.DEBUG) console.log(connection);

    await connection.open(this.WEB_SOCKET_TIMEOUT_MS);

    connection.addEventListener('message', async event => {
      let {messages, participants} = this.state;
      const messageData = JSON.parse(event.data);
      if(messageData.Type === 'sendmessage') {
        messages.push({
          id: messageData.MessageId,
          timestamp: messageData.Time,
          username: messageData.Sender,
          message: messageData.Content,
          vote: messageData.Vote,
        });

        this.setState({ messages });
      } else if(messageData.Type === 'upvote') {
        messages.map(message => {
          if(message.id === messageData.MessageId) {
            message.vote = messageData.Vote
          }

          return message;
        });

        this.setState({messages});
      } else if(messageData.Type === 'deletemessage') {
        let tempMessages = [];
        messages.map(message => {
          if(message.id !== messageData.MessageId) {
            tempMessages.push(message);
          }

          return message;
        });
        
        this.setState({messages: tempMessages});
      } else if(messageData.Type === 'getall') {
        let tempMessages = [];
        messageData.messages.map(message => {
          tempMessages.push({
            id: message.MessageId,
            timestamp: message.Time,
            username: message.Sender,
            message: message.Content,
            vote: message.Vote,
          });

          return message;
        });
        
        this.setState({messages: tempMessages});
      } else if(messageData.Type === 'raisehand') {
        participants.map(participant => {
          if(participant.attendeeId === messageData.Sender && this.role === 'host') {
            participant.raisehand = messageData.Data === 'false' ? true : false;
            this.setErrorMsg(`${participant.name} has raised their hand.`)
          }

          return participant;
        });
        this.setState({participants});

        if(messageData.Sender === this.joinInfo.Attendee.AttendeeId) {
            this.setState({handRaiseStatus: messageData.Data === 'false' ? false: true})
        }
      } else if(messageData.Type === 'downhand') {
        participants.map(participant => {
          if(participant.attendeeId === messageData.Data) {
            participant.raisehand = false;
          }

          return participant;
        });

        this.setState({participants});

        if(messageData.Data === this.joinInfo.Attendee.AttendeeId) {
          this.setState({handRaiseStatus: true});
          this.setErrorMsg("Your hand has been lowered.");
        }
      } else if(messageData.Type === 'muteremote') {
        if(this.joinInfo.Attendee.AttendeeId === messageData.Data) {
            this.props.chime.audioVideo.realtimeMuteLocalAudio();
            this.setErrorMsg('You have been muted by the host.');
        }
      } else if(messageData.Type === 'removeremote') {
        if(this.joinInfo.Attendee.AttendeeId === messageData.Data) {
            this.setErrorMsg('Host removed you!');
            await this.props.chime.leaveRoom(this.role === 'host');
            sessionStorage.removeItem(this.ssName);
            
            let blocks = JSON.parse(localStorage.getItem('blockedMeetings'));
            if(!blocks) {
                blocks = [];
            }

            blocks.push(this.joinInfo.Meeting.MeetingId);
            localStorage.setItem('blockedMeetings', JSON.stringify(blocks));


            const whereTo = `${this.baseHref}/${this.role === 'host' ? '' : 'removed'}`;
            this.props.history.push(whereTo);
            window.location.reload();
        }
      } else if(messageData.Type === 'makehost') {
        if(this.joinInfo.Attendee.AttendeeId === messageData.Data) {
            this.role = 'host';
            const ssData = JSON.parse(sessionStorage.getItem(this.ssName));
            ssData.role = this.role;
            sessionStorage.setItem(this.ssName, JSON.stringify(ssData));
            this.setErrorMsg('You are now a host!');
        }
      } else if(messageData.Type === 'gotolive') {
        this.setState({isLive: true, getStatus: 'received'});

        if(this.state.isOverFlow && this.state.getOverFlowStatus === 'received') return;
        
        await this.props.chime.joinRoom(this.audioElementRef.current);
        if(messageData.Host === this.joinInfo.Attendee.AttendeeId) {
            this.setErrorMsg('The session is now live!');
            if(this.state.isLive) {
              this.turnOnCamera();
            }
        } else {
            this.setErrorMsg('The session is now live!');
        }
      } else if(messageData.Type === 'getlivestatus') {
        this.setState({getStatus: 'received'})
        if(!this.state.isLive) {
            this.setState({isLive: messageData.status});
            if(messageData.status && !this.state.isOverFlow && this.state.getOverFlowStatus === 'received') {
                await this.props.chime.joinRoom(this.audioElementRef.current);
            }
        }
        
      } else if(messageData.Type === 'beforepart') {
        // if(!this.state.isLive && this.role === 'host') {
        if(this.joinInfo.Attendee.AttendeeId !== messageData.Sender) {
          let {participants} = this.state;
          let isExisting = false;
          participants.map(part => {
            if(part.attendeeId === messageData.Sender)
            {
              isExisting = true;
              let isVideoChat = messageData.isVideoChat === 'true' ? true : false;
              if(part.isVideoChat !== isVideoChat) {
                part.isVideoChat = isVideoChat;
              }
            }
            
            return part;
          })
          if(!isExisting) {
            if (participants.length >= MAX_REMOTE_VIDEOS - 1) {
              if(this.role === 'host') {
                data = `{
                  "message": "sendmessage",
                  "overflowId": "${messageData.Sender}",
                  "status": "overflowed",
                  "type": "overflow"
                }`;
                connection.send(data);
              }
              return;
            } else {
              if(this.role === 'host') {
                data = `{
                  "message": "sendmessage",
                  "overflowId": "${messageData.Sender}",
                  "status": "joinable",
                  "type": "overflow"
                }`;
                connection.send(data);
              }
            }
            participants.push({
              name: messageData.Name,
              mute: false,
              host: false,
              raisehand: false,
              attendeeId: messageData.Sender,
              isVideoChat: messageData.isVideoChat === 'true' ? true : false,
            });
          }

          this.setParts(participants);
        }
        
      } else if (messageData.Type === 'showableparts') {
        if(this.role !== 'host') {  
          // if(messageData.showableparts.length === 0) return;
          // let isExist = false;
          // messageData.showableparts.map(part => {
          //   if (this.state.showAbleParts.includes(part)) {
          //     isExist = true;
          //   } else {
          //     console.log(messageData.showableparts);
          //     this.setState({showAbleParts: messageData.showableparts});  
          //   }
          //   return part;
          // });

          // if (!isExist) {
          //   console.log(messageData.showableparts);
          //   this.setState({showAbleParts: messageData.showableparts});
          // }
          this.setState({showAbleParts: messageData.showableparts});
        }
      } else if(messageData.Type === 'overflow') {
        if(this.joinInfo.Attendee.AttendeeId === messageData.overflowId && this.state.getOverFlowStatus === 'waiting' && !this.state.isOverFlow) {
          this.setState({isOverFlow: messageData.status === 'overflowed' ? true : false, getOverFlowStatus: 'received'});

          if(messageData.status !== 'overflowed' && this.state.isLive && this.state.getStatus === 'received') {
            await this.props.chime.joinRoom(this.audioElementRef.current);
          }
        }
      } else if(messageData.Type === 'disconnected') {
        console.log(messageData);
        let { participants } = this.state;
        const parts = participants.filter((o) => o.attendeeId !== messageData.Sender);
        this.setParts(parts);
        // if(parts.length < MAX_REMOTE_VIDEOS -1 && this.state.showAbleParts.length < 9 && this.role === 'attendee' && this.state.isVideoChat === false) {
        //   this.setState({isAsking: true});
        // }
      }
      
    });

    let data = `{
      "message": "sendmessage",
      "type": "getall"
    }`;
    connection.send(data);

    data = `{
      "message": "sendmessage",
      "title": "${this.title}",
      "type": "getlivestatus"
    }`;
    connection.send(data);

    // if(!this.state.isLive && this.role !== 'host') {
    let me  =this;
    setInterval(function() {
      data = `{
        "message": "sendmessage",
        "name": "${me.username}",
        "isVideoChat": "${me.state.isVideoChat}",
        "type": "beforepart"
      }`;
      connection.send(data);

      if (me.role === 'host') {
        data = {
          "message": "sendmessage",
          "showableparts": me.state.showAbleParts,
          "type": "showableparts"
        };
        connection.send(JSON.stringify(data));
      }
      
    }, 5000)
      
    // }

    this.setState({ connection });
  }

  turnOnCamera = async () => {
    try {
      if (!this.props.chime.currentVideoInputDevice) {
        const videoInputDevices = await this.props.chime.audioVideo.listVideoInputDevices();
        let selectedDevice = videoInputDevices[0];
        if(videoInputDevices.length >= 2) {
          videoInputDevices.map(device => {
            if(device.label.toLowerCase().includes('front')) {
              selectedDevice = {
                label: device.label,
                value: device.deviceId
              };
            }

            return device;
          })
        }

        await this.props.chime.audioVideo.chooseVideoInputDevice(selectedDevice);
      } else {
        try {
          const videoInputDevices = await this.props.chime.audioVideo.listVideoInputDevices();

          let selectedDevice = this.props.chime.currentVideoInputDevice;
          if(videoInputDevices.length >= 2) {
            videoInputDevices.map(device => {
              if(device.label.toLowerCase().includes('front')) {
                selectedDevice = {
                  label: device.label,
                  value: device.deviceId
                };
              }

              return device;
            })
          }

          await this.props.chime.chooseVideoInputDevice(selectedDevice);

        } catch (err) {
          const videoInputDevices = await this.props.chime.audioVideo.listVideoInputDevices();
          await this.props.chime.audioVideo.chooseVideoInputDevice(videoInputDevices[0]);
        }
      }

      this.props.chime.audioVideo.startLocalVideoTile();
    } catch (error) {
      // eslint-disable-next-line
      console.error(error);
    }
  }
    openSettings = () => {
      this.setState({ showSettings: true });
    };

    closeSettings = () => {
        this.setState({ showSettings: false });
    };

    handleClick = (e) => {
        const { showSettings } = this.state;
        if (showSettings) {
            let node = e.target;
            let isModal = false;
            while (node) {
                if (
                    node &&
                    node.classList &&
                    node.classList.contains("modal__el")
                ) {
                    isModal = true;
                    break;
                }
                node = node.parentNode;
            }
            if (!isModal) {
                this.setState({ showSettings: false });
            }
        }
    };

    saveSettings = (
        playbackURL,
        currentAudioInputDevice,
        currentAudioOutputDevice,
        currentVideoInputDevice
    ) => {
        this.setState({
            showSettings: false,
            currentAudioInputDevice,
            currentAudioOutputDevice,
            currentVideoInputDevice,
        });
    };

    setMetadataId = (metadataId) => {
        this.setState({ metadataId });
    };

    setErrorMsg = (errorMsg, isClosable = true) => {
        this.setState({ errorMsg, showError: true, isClosable });
    };

    closeError = () => {
        this.setState({ showError: false });
    };

    endButtonOnClick = async () => {
      // if(this.role === 'host') {
      //   confirmAlert({
      //     title: 'Confirm to leave or end session',
      //     message: '',
      //     buttons: [
      //       {
      //         label: 'Leave Room',
      //         onClick: ()=> this.leaveroom()
      //       },
      //       {
      //         label: 'End Session',
      //         onClick: ()=> this.leaveroom(true)
      //       },
      //       {
      //         label: 'Cancel',
      //         onClick: () => console.log()
      //       }
      //     ]
      //   })
      // } else {
      //   confirmAlert({
      //     title: 'Confirm to leave',
      //     message: 'Are you sure you want to leave?',
      //     buttons: [
      //       {
      //         label: 'Leave Room',
      //         onClick: ()=> this.leaveroom()
      //       },
      //       {
      //         label: 'Cancel',
      //         onClick: () => console.log()
      //       }
      //     ]
      //   })
      // }

      this.setState({isAskForLeave: true});
    }

    askVideoOrObserver = () => {
      if(this.role !== 'host') {
      } else {
        this.initChatConnection();
      }
    }

    onVideoChat = () => {
      this.setState({
        isVideoChat: true,
        isAsking: false
      }); 
      this.initChatConnection();
    }

    onObserverChat = () => {
      this.setState({
        isVideoChat: false,
        isAsking: false
      }); 
      this.initChatConnection();
    }

    leaveroom = async (isEnd = false) => {
      await this.props.chime.leaveRoom(this.role === 'host' && isEnd);
      sessionStorage.removeItem(this.ssName);
      const whereTo = `${this.baseHref}/${this.role === 'host' && isEnd ? '' : 'join?room=' + this.title}`;
      this.props.history.push(whereTo);
      window.location.reload();
    }

    handleMute = (localMuted) => {
        this.setState({ muted: localMuted });
    }

    handlGoToLive = () => {
        const data = `{
          "message": "sendmessage",
          "title": "${this.title}",
          "type": "gotolive"
        }`;
        this.state.connection.send(data);
      }
    
    setVideo =() => {
      if (this.state.showAbleParts.length >= 10) {
        this.setErrorMsg('Sorry, all video chat slots are taken at the moment. Please wait for a slot to become free.');
        return;
      }
      this.setState({
        isVideoChat: true,
        isLocalCamera: true
      });

      this.turnOnCamera();
    }

    layout = () => {
        if (this.state.meetingStatus !== "Success") {
          return;
        }

        if (this.role === 'attendee' && (this.state.getOverFlowStatus === 'waiting' || this.state.getStatus === 'waiting')) {
          return;
        }

        return (
            <div className="meeting-grid" onClick={this.handleClick}>
                <div className="meeting-header">
                    <img className="header-logo" src={logo} alt="Logo" />
                    <div className="body-logo">
                        <span> SPONSORED BY: </span>
                        <img src={bodylogo} alt="body logo"/>
                    </div>
                </div>
                <div className="main-body">
                    {this.state.showSettings && (
                        <Settings
                            chime={this.props.chime}
                            joinInfo={this.joinInfo}
                            saveSettings={this.saveSettings}
                        />
                    )}
                    <div className="main-stage body-left">
                        <div className="body-before">
                            <div className="body-wrapper">
                                {
                                  this.state.isVideoChat && 
                                  <LocalVideo
                                    chime={this.props.chime}
                                    joinInfo={this.joinInfo}
                                    isLive={this.state.isLive}
                                    isLocalCamera={this.state.isLocalCamera}
                                  />
                                }
                                
                                <div className="mobile-buttons">
                                    {(!this.state.isVideoChat ) &&
                                     <button className="join-video-mobile" onClick={this.setVideo}>
                                        <span className="txt-onbtn">Join Video Chat Now</span>
                                    </button>}

                                    {(!this.state.isLive && this.role === 'host') &&
                                     <button className="go-livebtn" onClick={this.handlGoToLive}>
                                        <span className="txt-onbtn">GO LIVE</span>
                                    </button>}
                                   
                                   {
                                     this.state.isLive &&
                                      <button className="live-meetingbtn txt-onbtn" onClick={this.endButtonOnClick}>
                                        <span className="txt-onbtn">{this.role === 'host' ? 'End Session' : 'LEAVE MEETING'}</span>
                                      </button>
                                   }
                                    
                                </div>
                                
                                <div className="remote-cams">
                                  <RemoteVideoGroup
                                    chime={this.props.chime}
                                    joinInfo={this.joinInfo}
                                    participants={this.state.participants}
                                    setParts={this.setParts}
                                    isOpenChat={this.state.showPopupMessage}
                                    showAbleParts={this.state.showAbleParts}
                                  />
                                </div>
                                
                            </div>
                        </div>
                        <div className="meeting-footer">
                          <Controls
                            chime={this.props.chime}
                            baseHref={this.baseHref}
                            ssName={this.ssName}
                            title={this.title}
                            openSettings={this.openSettings}
                            role={this.role}
                            history={this.props.history}
                            myVideoElement={this.myVideoElement}
                            openChat={this.openChat}
                            openPT={this.openPT}
                            closeChat={this.hideModal}
                            isOpenChat={this.state.showPopupMessage}
                            joinInfo={this.joinInfo}
                            connection={this.state.connection}
                            handRaiseStatus={this.state.handRaiseStatus}
                            muted={this.state.muted}
                            setMute={this.handleMute}
                            isLive={this.state.isLive}
                            isLocalCamera={this.state.isLocalCamera}
                            setLocalCamera={this.setLocalCamera}
                            isVideoChat={this.state.isVideoChat}
                            setVideo={this.setVideo}
                            turnOnCamera={this.turnOnCamera}
                          />
                        </div>
                    </div>
                    {/* {
                      this.state.isVideoChat &&  */}
                      <div className="right-sidebar">
                        {!this.state.showPopupMessage ? (
                            " "
                        ) : (
                            <button className={"btnPopdownMessage btnPopupMessageFont"} onClick={this.hideModal}>
                                <img src={vicon} alt="v right" className={'vicon vright'}/>
                            </button>
                        )}
                        {this.state.showPopupMessage ? (
                            " "
                        ) : (
                            <button className={"btnPopupMessage btnPopupMessageFont"} onClick={this.showModal}>
                                <img src={vicon} alt="v left" className={'vicon vleft'}/>
                                <span>{this.role === 'host' ? 'Q&A/PARTICIPANTS': 'Q&A'}</span>
                            </button>
                        )}
                        {!this.state.showPopupMessage ? (
                            " "
                        ) : (
                            <Chat
                              chime={this.props.chime}
                              title={this.title}
                              username={this.username}
                              joinInfo={this.joinInfo}
                              closeChat={this.hideModal}
                              isChat={this.state.isChat}
                              changeTab={this.changeChat}
                              connection={this.state.connection}
                              messages={this.state.messages}
                              participants={this.state.participants}
                              role={this.role}
                              setErrorMsg={this.setErrorMsg}
                            />
                        )}
                      </div>
                    {/* } */}
                    
                </div>
            </div>
        );
    };

    setLocalCamera = (status) =>{
      this.setState({isLocalCamera: status});
    }

    setParts = (participants) => {
      this.setState({participants});

      if (this.role === 'host') {
        let showAbleParts = [];

        showAbleParts.push(this.joinInfo.Attendee.AttendeeId);
        participants.map(part => {
          if(showAbleParts.length <= 9 && part.host) {
            showAbleParts.push(part.attendeeId);
          }
  
          return part;
        });

        participants.map(part => {
          if(showAbleParts.length <= 9 && part.isVideoChat) {
            showAbleParts.push(part.attendeeId);
          }
  
          return part;
        });

        this.setState({showAbleParts});
      }

      if(!this.state.showAbleParts.includes(this.joinInfo.Attendee.AttendeeId) && this.state.showAbleParts.length >= 10) {
        // this.setErrorMsg('Sorry, all video chat slots are taken at the moment. Please wait for a slot to become free.');
        this.setState({isVideoChat: false});
      }
    }

    render() {
      const {isAvailable, isLive, isAsking, isOverFlow, getStatus, getOverFlowStatus, isClosable} = this.state;



      if(isAsking && this.role === 'attendee') return <AlertAskVideoObserver onVideoChat={this.onVideoChat} onObserverChat={this.onObserverChat}/>

      if(this.role === 'attendee' && !isLive && getStatus === 'received') {
        return <AlertWaiting />
      }

      if(isOverFlow && getOverFlowStatus === 'received') {
        return <AlertHolding />
      }

      return (
        <React.Fragment>
          {isAvailable && <Alert />}
          {/* {(this.role === 'attendee' && !isLive && getStatus === 'received') && <AlertWaiting />} */}
          
          {
            this.state.isAskForLeave && 
            <AlertAskLeaveRoom 
              role={this.role}
              onLeave={() => this.leaveroom(false)}
              onCancel={() => this.setState({isAskForLeave: false})}
              onEndSession={() => this.leaveroom(true)}
            />
          }
          <audio ref={this.audioElementRef} style={{ display: "none" }} />

          {this.layout()}

          {this.state.showError && (
            <Error
              closeError={this.closeError}
              errorMsg={this.state.errorMsg}
              isClosable={isClosable}
            />
          )}
        </React.Fragment>
      );
    }
}

Meeting.propTypes = {
  chime: PropTypes.object,
};

export default withRouter(Meeting);
