import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import PropTypes from 'prop-types';

import "./Alert.css";
import removepart from "./../../assets/svgs/bin.svg";
import cancelalert from "./../../assets/svgs/cancel-alert.svg";

class AlertAskRemovePart extends Component {

    render() {
        return (
            <div className="ask-alert">
                <div className="ask-alert-backdrop" onClick={this.props.onCancel}></div>
                
                <div className="ask-alert-container">
                    <div className="ask-alert-header">
                       Are you sure you want <br/> to remove this participant?
                    </div>

                    <div className="ask-alert-content">
                        <div className="ask-alert-item remove-ask-item" onClick={this.props.onRemove}>
                            <img src={removepart} alt="video chat"/>
                            <span className="ask-alert-item-text">Remove participant</span>
                        </div>

                        <div className="ask-alert-item" onClick={this.props.onCancel}>
                            <img src={cancelalert} alt="cancel"/>
                            <span className="ask-alert-item-text">Cancel</span>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

AlertAskRemovePart.propTypes = {
    onRemove: PropTypes.func,
    onCancel: PropTypes.func,
};

export default withRouter(AlertAskRemovePart);