import React, { Component } from "react";
import { withRouter } from "react-router-dom";

// Styles
import "./Alert.css";
import logo from "./../../assets/Logo.svg";

class AlertHolding extends Component {

    render() {
        return(
        <div className={"alert-container"}>
            <div className={"alert-back-drop"}>
            </div>
            <div className={"alert-content"}>
                <img className="alert-logo" src={logo} alt="Logo" />
                <p>Sorry, this virtual exhibition booth is currently full.</p>
                <p>Please try again in 5 minutes.</p>
                <p>We'd recommend joining another exhibitor's booth while you wait.</p>
            </div>
        </div>
        );
        
    }
}

export default withRouter(AlertHolding);