import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import PropTypes from 'prop-types';

import "./Alert.css";
import leaveroom from "./../../assets/svgs/leave-room.svg";
import cancelalert from "./../../assets/svgs/cancel-alert.svg";
import endsession from "./../../assets/svgs/end-session-icon.svg";


class AlertAskLeaveRoom extends Component {

    render() {
        return (
            <div className="ask-alert">
                <div className="ask-alert-backdrop" onClick={this.props.onCancel}></div>
                
                <div className="ask-alert-container">
                    <div className="ask-alert-header">
                        {
                            this.props.role === 'host' ? 'Do you want to leave or end the session for all participants?' : 'Are you sure you want to leave?'
                        }
                    </div>

                    <div className="ask-alert-content">
                        <div className="ask-alert-item" onClick={this.props.onLeave}>
                            <img src={leaveroom} alt="leave room"/>
                            <span className="ask-alert-item-text">Leave room</span>
                        </div>

                        {
                            this.props.role === 'host' && 
                            <div className="ask-alert-item endsession-ask-item" onClick={this.props.onEndSession}>
                                <img src={endsession} alt="end session"/>
                                <span className="ask-alert-item-text">End Session for all participants</span>
                            </div>
                        }

                        <div className="ask-alert-item" onClick={this.props.onCancel}>
                            <img src={cancelalert} alt="cancel"/>
                            <span className="ask-alert-item-text">Cancel</span>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

AlertAskLeaveRoom.propTypes = {
    onLeave: PropTypes.func,
    onCancel: PropTypes.func,
    onEndSession: PropTypes.func,
    role: PropTypes.string,
};

export default withRouter(AlertAskLeaveRoom);